#content .leadImage {
  display: none;
}
#search-app .searchapp-wise .sui-layout-body {
  background-color: transparent;
}
#search-app .searchapp-wise .sui-layout-body__inner {
  max-width: 100%;
  padding: 0;
}
#search-app .searchapp-wise .sui-layout-header {
  padding-right: 0;
  padding-left: 0;
}
#search-app .searchapp-wise .sui-facet {
  font-family: "RobotoL" !important;
}
#search-app .searchapp-wise .sui-facet__title {
  padding-top: 2em;
  border-top: 1px solid #ddd;
  border-bottom: none;
  margin-top: 2em !important;
}
#search-app .searchapp-wise .sui-multi-checkbox-facet {
  margin-bottom: 0;
  color: #4d4d4d;
  font-size: 14px;
  font-weight: normal !important;
}
#search-app .searchapp-wise .sui-multi-checkbox-facet__checkbox {
  margin-right: 8px;
}
#search-app .searchapp-wise .sui-sorting {
  margin-bottom: 32px;
}
#search-app .searchapp-wise .sui-facet__title,
#search-app .searchapp-wise .sui-sorting__label {
  margin-bottom: 5px;
  color: #002d54;
  font-family: "PoppinsB" !important;
  font-size: 14px;
}
#search-app .searchapp-wise .sui-paging-info,
#search-app .searchapp-wise .sui-results-per-page,
#search-app .searchapp-wise .sui-select,
#search-app .searchapp-wise .sui-search-box,
#search-app .searchapp-wise .sui-facet-search__text-input,
#search-app .searchapp-wise .sui-facet-view-more {
  font-family: "PoppinsR" !important;
  font-size: 14px;
}
#search-app .searchapp-wise .sui-facet-view-more {
  color: #0077bc;
}
#search-app .searchapp-wise .sui-select__value-container--has-value {
  font-family: "PoppinsSemiB" !important;
}
#search-app .searchapp-wise .sui-search-box {
  padding: 0px;
  display: flex;
}
#search-app .searchapp-wise .sui-search-box__submit {
  border: none;
  background-color: #002d54;
  color: #fff;
  padding: 6px 12px;
}
#search-app .searchapp-wise .sui-search-box__submit:hover {
  background: none;
  background-color: #005588 !important;
  color: #fff;
}
#search-app .searchapp-wise .sui-search-box__text-input {
  padding: 10px;
}
#search-app .searchapp-wise .listing-view-item.header {
  cursor: pointer;
}
#search-app .searchapp-wise .listing-view-details .listing-view-details-section {
  padding: 1em 0em;
}
#search-app .searchapp-wise .listing-view-details .listing-view-details-section .details-field {
  display: flex;
}
#search-app .searchapp-wise .listing-view-details .listing-view-details-section .details-field:first-of-type {
  border-bottom: none;
}
#search-app .searchapp-wise .listing-view-details .details-field {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
#search-app .searchapp-wise .listing-view-details .listing-view-details-section .details-field .details-field-label {
  width: 20%;
  padding: 0.3em 1em 0.3em 0.3em;
  border-right: 1px solid #ddd;
  font-family: "RobotoM";
}
#search-app .searchapp-wise .listing-view-details .listing-view-details-section .details-field .details-field-value {
  width: 80%;
  padding: 0.3em 0.3em 0.3em 1em;
}
#search-app .searchapp-wise .item {
  margin-top: 2em;
}
#search-app .searchapp-wise .rc-pagination-item-active,
#search-app .searchapp-wise .rc-pagination-item-active:hover {
  background-color: #064c7f;
  color: #fff;
}
#search-app .searchapp-wise a,
#search-app .searchapp-wise a:hover {
  color: inherit !important;
}
#search-app .searchapp-wise input:focus,
#search-app .searchapp-wise .sui-select__control--is-focused {
  border: 1px solid #6bc6fa !important;
}
#search-app .searchapp-wise .ui.container.view-selector {
  margin: 0em 0em 2em 0em !important;
  padding: 0px !important;
}
#search-app .searchapp-wise .ui.container.view-selector .ui.button {
  font-size: 14px;
}
#search-app .searchapp-wise .header.listing-view-item {
  cursor: pointer;
}
.sui-layout-main-body,
.ui.modal > .content {
  margin-bottom: 2em;
  color: rgba(0, 0, 0, 0.87);
  font-family: "RobotoL";
  font-size: 18px;
}
.ui.modal > .content {
  font-size: 18px !important;
}
.sui-layout-main-body .item h4,
.ui.modal > .header {
  color: #002d54 !important;
  font-family: "PoppinsSemiB" !important;
  font-size: 20px !important;
}
.ui.accordion .title:not(.ui) {
  font-size: 1.2em;
  font-weight: bold;
}
